<template>
  <div class="container px-4 px-lg-5">
    <div class="py-5 text-center">
      <h2></h2>
      <p class="lead">     .</p>
    </div>

    <div class="form-floating">
      <input type="email" class="form-control" id="floatingInput" v-model="user.userId">
      <label for="floatingInput"></label>
      <div class="validation-div" v-if="errorMsgBag.userId">{{ errorMsgBag.userId }}</div>
    </div>
    <div class="form-floating mb-5">
      <input type="password" class="form-control" id="floatingPassword" v-model="user
      .userPw">
      <label for="floatingPassword"></label>
      <div class="validation-div" v-if="errorMsgBag.userPw">{{ errorMsgBag.userPw }}</div>
    </div>

    <div class="py-5">
      <button class="w-100 btn btn-lg btn-primary mb-3" @click="login"></button>
      <router-link to="/signupvue"><button class="w-100 btn btn-lg btn-success"></button></router-link>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      user: {
        userId: '',
        userPw: ''
      },
      errorMsgBag: {}
    }
  },
  setup() { },
  created() { },
  mounted() { },
  unmounted() { },
  methods: {
    login() {
      this.$store.dispatch('auth/login', this.user).then(() => {
        this.$router.push({ path: '/' })
        alert(' .')
      },
      (error) => {
        if (error.response.status === 401) {
          alert('    .')
        }
        this.errorMsgBag = error.response.data
      })
    }

  }
}
</script>
