<template>
  <div class="container px-4 px-lg-5">
    <div class="py-5 text-center">
      <h2></h2>
      <p class="lead">   .</p>
    </div>

    <div class="row g-5">
      <div class="col-12">
        <div class="row g-3">
          <div class="col-12">
            <label for="userId" class="form-label"></label>
            <input type="text" class="form-control" id="userId" placeholder=" ." v-model="user.userId">
            <div class="validation-div" v-if="errorMsgBag.userId">{{ errorMsgBag.userId }}</div>
            <div class="validation-div" v-if="errorMsgBag.userIdDuple">{{ errorMsgBag.userIdDuple }}</div>
          </div>
          <div class="col-12">
            <label for="userNickNm" class="form-label"></label>
            <input type="text" class="form-control" id="userNickNm" placeholder=" ."
              v-model="user.userNickNm">
            <div class="validation-div" v-if="errorMsgBag.userNickNm">{{ errorMsgBag.userNickNm }}</div>
          </div>
          <div class="col-12">
            <label for="userPw" class="form-label"></label>
            <input type="password" class="form-control" id="userPw" placeholder=" ."
              v-model="user.userPw">
            <div class="validation-div" v-if="errorMsgBag.userPw">{{ errorMsgBag.userPw }}</div>
          </div>
          <div class="col-12">
            <label for="email" class="form-label"></label>
            <input type="email" class="form-control" id="email" placeholder=" ." required=""
              v-model="user.userEmail">
            <div class="validation-div" v-if="errorMsgBag.userEmail">{{ errorMsgBag.userEmail }}</div>
          </div>
          <div class="col-12">
            <input type="checkbox" class="form-check-input" id="isAdult" v-model="user.adult">
            <label class="form-check-label" for="isAdult">  .</label>
          </div>
        </div>
        <hr class="my-4">
        <button class="w-100 btn btn-primary btn-lg mb-5" @click="signUp"></button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      user: {
        userId: '',
        userNickNm: '',
        userPw: '',
        userEmail: '',
        adult: ''
      },
      errorMsgBag: {}
    }
  },
  setup() { },
  created() { },
  mounted() { },
  unmounted() { },
  methods: {
    signUp() {
      this.$store.dispatch('auth/signup', this.user).then(() => {
        this.$router.push({ path: '/' })
        alert(' .      .')
      },
      (error) => {
        this.errorMsgBag = error.response.data
      })
    }
  }
}
</script>
